import React, {Component} from 'react';
import {CellButton} from "@vkontakte/vkui";

class Matches extends Component {

    constructor(props){
        super(props);
        this.state = {
            isSelectedFriends: true
        };
    }

    onSwitchClick = () => {
        let updatedSelection = !this.state.isSelectedFriends;
        this.setState({isSelectedFriends:updatedSelection});
    };

    render() {
        return (
            <div>
                <CellButton onClick={this.onSwitchClick} >
                    Hi
                </CellButton>

                <div>
                    {this.state.isSelectedFriends ? 'currently' : 'not'}
                </div>
            </div>
        );
    }
}

export default Matches;
